.training__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 3rem 0 3rem;
  max-width: 1920px;
  width: 100%;
  margin: 10rem auto;
}

.training__container-title {
  margin-bottom: 0.5rem;
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
  transition: all 0.3s ease-in-out;
  background: transparent;
  scroll-margin-top: 1rem;
}

.training__container-paragraph {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.5rem;
  color: rgba(254, 255, 179, 0.7);
  margin-bottom: 2.5rem;
  letter-spacing: 4px;
  transition: all 0.3s ease-in-out;
  background: transparent;
}

.training__container-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 2rem;
  row-gap: 2.5rem;
}

.training__container-item {
  display: flex;
  max-width: 56rem;
  height: 33rem;
  border: 2px solid #fbe0f3;
  padding: 2.5rem 0rem 2.5rem 2.5rem;
}

.training__container-item img {
  padding: 0 2.5rem 0 0;
  max-width: 300px;
  object-fit: cover;
}

.training__container-sub-title {
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 2.5rem;
  color: #feffb3;
  padding: 0 0 1.5rem 0;
  text-align: start;
}

.training__container-sub-paragraph {
  font-size: 1rem;
  font-weight: 200;
  line-height: 1rem;
  letter-spacing: 0.25rem;
  color: rgba(251, 224, 243, 0.7);
  text-align: start;
  padding-bottom: 1rem;
}

.training__container-buy-btn {
  border: 2px solid #fbe0f3;
  border-radius: 29px;
  display: flex;
  color: #fbe0f3;
  background: transparent;
  padding: 1rem 3.5rem 1rem 3.5rem;
  letter-spacing: 2.4px;
  transition: all 0.25s ease-in-out;
  white-space: nowrap;
}

.training__container-buy-btn span {
  color: #fbe0f3;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  white-space: nowrap;
}

.training__container-buy-btn:hover,
.training__container-buy-btn:active {
  background: #e18186;
  color: black;
  border: 2px solid #e18186;
}

.training__container-buy-btn span {
  transition: all .25s ease-in-out;
}

.training__container-buy-btn:hover span {
  color: black;
}

.training__container-buy-btn svg {
  width: 1.25rem;
  height: 1.25rem;
  padding: 0;
  margin-left: 1.5rem;
}

.training__container-subitem {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.training__container-limited {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 300;
  letter-spacing: 4px;
  color: rgba(254, 255, 179, 0.7);
  margin-top: 2.5rem;
}

.training__container-buy-container {
  display: flex;
  flex-direction: column;
  margin-top: auto;
  align-items: start;
  gap: 2rem;
}

.training__container-price {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 400;
  color: #feffb3;
}

.training__container-buy-btn-added {
  background: #e18186;
  color: black;
  border: 2px solid #e18186;
  border-radius: 29px;
  display: flex;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  padding: 1rem 3.5rem 1rem 3.5rem;
  letter-spacing: 2.4px;
  transition: color 0.25s ease-in-out;
  white-space: nowrap;
}

@media (max-width: 768px) {
  .training__container {
    max-width: none;
    margin: 5.5rem auto;
  }

  .training__container-title {
    font-size: 1rem;
    line-height: 1.25rem;
    margin: 0;
  }

  .training__container-paragraph {
    font-size: .5rem;
    line-height: .5rem;
    font-weight: 500;
    letter-spacing: 2px;
  }

  .training__container-content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  .training__container-sub-title {
    font-size: 1rem;
    line-height: 1rem;
    letter-spacing: 1px;
    padding-bottom: 1rem;
  }

  .training__container-item img {
    max-width: 8rem;
    max-height: 11rem;
    padding-right: .5rem;
  }

  .training__container-sub-paragraph {
    font-size: .5rem;
    font-weight: 400;
    line-height: .5rem;
    letter-spacing: 1px;
    padding-bottom: .5rem;
  }

  .training__container-item {
    max-width: 22.5rem;
    width: auto;
    height: auto;
    padding: .5rem;
    border: 1px solid #fbe0f3;
  }

  .training__container-buy-btn, .training__container-buy-btn-added {
    font-size: .5rem;
    line-height: .5rem;
    letter-spacing: 2px;
    padding: .5rem 2rem;
    border: 1px solid #fbe0f3;
  }

  .training__container-buy-btn svg, .training__container-buy-btn-added svg {
    max-width: .5rem;
    max-height: .5rem;
    margin-left: .5rem;
  }

  .training__container-buy-btn span {
    display: none;
  }

  .training__container-buy-btn::before {
    content: "КУПИТЬ";
    font-size: .5rem;
    letter-spacing: 2px;
    line-height: .5rem;
    font-weight: 400;
    font-family: "Onest", sans-serif;
  }

  .training__container-price {
    font-size: 1rem;
    font-weight: 300;
    line-height: 1rem;
    letter-spacing: 1px;
    white-space: nowrap;
    text-decoration: underline;
  }

  .training__container-buy-container {
    flex-direction: row;
    gap: 1rem;
    align-items: flex-end;
    margin-bottom: .5rem;
  }

  .training__container-buy-btn:hover,
  .training__container-buy-btn:active {
    border: 1px solid #e18186;
  }

  .training__container-paragraph {
    font-size: .5rem;
    line-height: .5rem;
    font-weight: 400;
    margin-bottom: 1.5rem;
  }

  .training__container-limited {
    font-size: .75rem;
    font-weight: 300;
    line-height: .75rem;
    margin: 1rem 0 0 0;
  }
}