.input_container {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1.5rem;
  margin-bottom: 1rem;
  height: auto;
}

.input {
  background: transparent;
  border: 2px solid #fbe0f3;
  outline: none;
  height: 100%;
  max-width: 44.5rem;
  width: 100%;
  font-size: 1.5rem;
  color: #fbe0f3;
  padding: 1.5rem 1.5rem 1.5rem 1rem;
}

.input::placeholder {
  color: rgba(251, 224, 243, 0.5);
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: start;
  border: 0;
  letter-spacing: 4px;
}

.input_wrapper {
  position: relative;
  width: 100%;
}

.utils {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  letter-spacing: 4px;
  line-height: 1.5rem;
  font-weight: 400;
  cursor: pointer;
}

.utils P:nth-of-type(1) {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.login-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.error {
  color: #eb443c;
  max-height: 0;
  height: 100%;
  overflow: hidden;
  transition:
    max-height 0.3s ease-in-out,
    opacity 0.3s ease-in-out;
  font-size: 1rem;
  padding: 0;
  margin: 0;
  font-weight: 400;
  letter-spacing: 4px;
  opacity: 0;
  text-align: center;
  line-height: 1.5rem;
  white-space: pre-line;
}

.input_container .error {
  margin-top: -0.5rem;
}

.error_border {
  border-color: rgba(201, 42, 21, 0.85);
  outline: none;
}

.btn {
  padding: 1rem 11rem 1rem 11rem;
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 0;
  background: transparent;
  border: 2px solid #fbe0f3;
  color: #fbe0f3;
  cursor: pointer;
  letter-spacing: 0.25rem;
  display: flex;
  align-items: center;
  border-radius: 2rem;
  margin-top: 3.5rem;
  margin-bottom: 1rem;
}

.btn svg {
  margin-left: 1.5rem;
}

.visible {
  opacity: 1;
  max-height: 1.5rem;
}

.utils .error.visible {
  max-height: 3rem;
}
