.cart-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  background: transparent;
  height: 100%;
  width: 100%;
  z-index: 9999;
  display: flex;
  justify-content: end;
  overflow-y: scroll;
  box-sizing: border-box;
  align-items: flex-start;
  color-scheme: dark;
  opacity: 1;
  transition: all .5s ease-out;
}

.background {
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(16, 16, 16, 0.8);
  height: 100%;
  width: 100%;
  z-index: 9998;
  transition: opacity .3s ease-out, visibility .3s ease-out;
  opacity: 1;
  visibility: visible;
}

.invisible {
  transform: translateX(100%);
}

.invisible_bg {
  opacity: 0;
  visibility: hidden;
}


.cart-container {
  background: #222023;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 58.5rem;
  position: relative;
}

.exit-btn {
  background: none;
  border: 0;
  padding: 0;
  margin: 0;
  cursor: pointer;
  width: fit-content;
  margin: 1.75rem 3rem 1.75rem auto;
}

.title {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
  margin-top: 0.5rem;
  text-align: start;
}

.card-container {
  display: flex;
  padding: 1rem;
  border: 2px solid #fbe0f3;
  margin: 0 3rem 4rem 3rem;
  min-width: 52.5rem;
}

.img {
  margin-right: 1.5rem;
  max-width: 8rem;
  max-height: 11.5rem;
}

.delete-btn {
  padding: 0;
  margin: 0;
  border: 0;
  background: none;
  cursor: pointer;
}

.price {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: #feffb3;
  letter-spacing: 4px;
  text-decoration: underline;
}

.utils {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: end;
}

.last-price-container {
  margin-left: 3rem;
  display: flex;
  flex-direction: column;
  color: #feffb3;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1rem;
  letter-spacing: 4px;
  gap: 0.5rem;
}

.last-price {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: #feffb3;
  text-decoration: underline;
}

.buy-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: auto 3rem 5rem 0;
  padding-top: 2rem;
}

.buy-btn {
  background: none;
  border: 2px solid #fbe0f3;
  border-radius: 20rem;
  padding: 1rem 10rem 1rem 10rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  color: #fbe0f3;
  font-weight: 400;
  letter-spacing: 4px;
  line-height: 1.5rem;
}

.buy-btn svg {
  max-width: 1.5rem;
  max-height: 1.5rem;
}

.cart-nothing-title {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
  text-align: start;
  color: #fbe0f3;
}

.cart-nothing-paragraph {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 400;
  letter-spacing: 4px;
  color: #fbe0f3;
  text-align: start;
}

.cart-nothing-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: -1rem 0 0 3rem;
  flex-grow: 1;
}

.catalog-btn {
  background: none;
  border: 2px solid #fbe0f3;
  border-radius: 20rem;
  padding: 1rem 3rem 1rem 3rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  color: #fbe0f3;
  font-weight: 400;
  letter-spacing: 4px;
  line-height: 1.5rem;
  width: fit-content;
  align-self: flex-end;
  margin: auto 3rem 5rem 0;
}