.lk__wrapper {
  background: url(/public/images/Frame.png), #222023;
  max-width: 1905px;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.lk__inside-wrapper {
  padding: 0 3rem 0 3rem;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.lk__title {
  text-align: start;
  margin-top: 5rem;
  color: #feffb3;
  font-size: 3.5rem;
  line-height: 4rem;
  font-weight: 400;
}

.lk__paragraph {
  margin-top: 1rem;
  margin-bottom: 5rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 300;
  color: rgba(251, 224, 243, 0.8);
  letter-spacing: 4px;
}

.lk__my-trainig {
  color: #feffb3;
  font-size: 3.5rem;
  line-height: 4rem;
  font-weight: 400;
  margin-bottom: 2.5rem;
}

.lk__trainings {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  row-gap: 4rem;
}

.lk__trainings-element:nth-child(odd) {
  justify-self: start;
}

.lk__trainings-element:nth-child(even) {
  justify-self: end;
}

.lk__trainings-element {
  display: flex;
  border: 2px solid #fbe0f3;
  max-width: 52.5rem;
  width: 100%;
}

.lk__trainings-element img {
  padding: 1rem 1.5rem 1rem 1rem;
}

.lk__trainings-element-wrapper {
  padding: 1.5rem 1.5rem 1.5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.lk__trainings-element-second-wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.lk__trainings-element-second-wrapper p {
  font-size: 2rem;
  color: #fbe0f3;
  font-weight: 400;
  line-height: 2rem;
}

.lk__trainings-element-time {
  text-align: end;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.lk__trainings-element-time p {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  color: #feffb3;
  letter-spacing: 2px;
  text-decoration: underline;
}

.lk_trainings-element-extend-btn {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  letter-spacing: 2px;
  color: rgba(251, 224, 243, 0.5);
  text-align: end;
  border: none;
  padding: 0;
  margin: 0;
  outline: none;
  background: transparent;
  text-decoration: underline;
  white-space: nowrap;
  cursor: pointer;
}

.lk__trainings-element-proceed-btn {
  background: transparent;
  outline: none;
  border: 0;
  padding: 0;
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: 4px;
  text-decoration: underline;
  align-self: flex-end;
  margin-top: auto;
  cursor: pointer;
}

.exit_button {
  outline: none;
  border: 0;
  margin: 5rem 0 0 0;
  padding: 0;
  background: transparent;
  color: rgba(254, 255, 179, 0.5);
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.5rem;
  letter-spacing: 4px;
  cursor: pointer;
  transition: all .3s ease-out;
}

.exit_button:hover, .exit_button:focus {
  color: #feffb3;
}

.lk__utils {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.empty_cart h2 {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
  color: #FBE0F3;
  text-align: start;
  margin-bottom: 1rem;
}

.empty_cart {
  display: flex;
  flex-direction: column;
}

.empty_cart p {
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: 4px;
  line-height: 1.5rem;
  color: #FBE0F3;
  text-align: start;
}

.empty_cart button {
  background: transparent;
  border: 2px solid #FBE0F3;
  border-radius: 100rem;
  color: #FBE0F3;
  padding: 1rem 9rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
  margin: 5rem auto;
  cursor: pointer;
  letter-spacing: 4px;
}

.empty_cart button svg {
  margin-left: .5rem;
}