.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 44.5rem;
  width: 100%;
  z-index: 10;
  border-radius: 3px;
  transform: translateY(11rem);
}

.wrapper {
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  background: url(/public/images/Frame.png), #222023;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 5;
}

.wrapper_header {
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1920px;
  width: 100%;
  padding: 0 4rem 0 3rem;
}

.wrapper_header button {
  color: black;
  background: none;
  border: 0;
  padding: 0;
  display: inline-block;
  cursor: pointer;
  height: 2rem;
  color: white;
  transition: all 0.15s ease-in-out;
}

.wrapper_header h2 {
  font-size: 2rem;
  font-weight: 400;
  color: #feffb3;
  padding: 1rem 0 1.25rem 0rem;
  line-height: 2.375rem;
  cursor: pointer;
}

.title {
  width: 100%;
  text-align: start;
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: 0.25rem;
  padding-bottom: 3rem;
}

.selected {
  text-decoration: underline;
  text-underline-offset: 7px;
  text-decoration-thickness: 2px;
  color: #feffb3 !important;
}

.nav-button {
  background: transparent;
  border: none;
  padding: 0;
  color: rgba(254, 255, 179, 0.5);
  font-weight: 400;
  font-size: 2.5rem;
}

.buttons {
  display: flex;
  width: 100%;
  gap: 4rem;
  padding-bottom: 3rem;
}

.agreement {
  text-decoration: underline;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1rem;
  background: transparent;
  border: none;
  padding: 0;
  color: #fbe0f3;
  cursor: pointer;
  letter-spacing: 1px;
  text-wrap: nowrap;
}

.input {
  background: transparent;
  border: 2px solid #fbe0f3;
  outline: none;
  min-height: 4.625rem;
  height: 100%;
  max-width: 45.5rem;
  width: 100%;
  font-size: 1.5rem;
  color: #fbe0f3;
  padding: 0 1.5rem 0 1.5rem;
}

.input::placeholder {
  color: rgba(251, 224, 243, 0.5);
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: start;
  border: 0;
  letter-spacing: 4px;
  padding: 1.875rem 0 1.875rem 0;
}

.btn {
  padding: 1rem 9.875rem 1rem 9.875rem;
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 1.75rem;
  background: transparent;
  border: 2px solid #fbe0f3;
  color: #fbe0f3;
  cursor: pointer;
  letter-spacing: 0.25rem;
  display: flex;
  align-items: center;
  border-radius: 1.5rem;
  margin-top: 3.5rem;
  margin-bottom: 1rem;
}

.btn svg {
  margin-left: 1.5rem;
}

.toggle {
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
  width: 2rem;
  height: 2rem;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}
