.questions__container {
  padding: 0 3rem;
  cursor: pointer;
  max-width: 1905px;
  width: 100%;
  margin: 10rem auto 5.5rem auto;
}

.questions__container-title {
  margin-bottom: 2.5rem;
}

@media (max-width: 768px) {
  .questions__container-title, .questions__container-title span {
    font-size: 1rem;
    line-height: 1.25rem;
    margin-bottom: 1.5rem;
  }

  .questions__container {
    margin: 5rem auto 5.5rem auto;
    max-width: none;
  }
}