.container {
  width: 27.5rem;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: solid #fbe0f3 2px;
  border-radius: 3px;
  text-align: center;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 1.5rem;
  white-space: pre-wrap;
  letter-spacing: 2px;
}

.number {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: #fbe0f3;
}

.container:hover,
.container:active {
  background: #e18186;
  border: 2px solid #e18186;
  color: #222023;
}

.container:hover p,
.container:active p {
  color: #222023;
}

.text,
.number,
.container {
  transition: all 0.25s ease-in-out;
}

.container:hover .number,
.container:active .number {
  color: #222023;
}

.text {
  background: transparent;
  font-size: 1.25rem;
  line-height: 1.25rem;
  letter-spacing: 1.5px;
}

@media (max-width: 768px) {
  .container {
    max-width: 10.5rem;
    max-height: 4rem;
    flex-shrink: 0;
    border: solid #fbe0f3 1px;
  }

  .number, .text {
    font-size: .5rem;
    font-weight: 300;
    line-height: .5rem;
    letter-spacing: 1px;
  }

  .number {
    padding: 0.5rem;
  }
}