.container {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form_wrapper {
  display: flex;
  flex-direction: column;
  display: inline-block;
  transform: translateY(35%);
}

.wrapper_header {
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1920px;
  width: 100%;
  padding: 0 3rem 0 3rem;
}

.wrapper_header button {
  color: black;
  background: none;
  border: 0;
  padding: 0;
  display: inline-block;
  cursor: pointer;
  height: 2rem;
  color: white;
  transition: all 0.15s ease-in-out;
}

.wrapper_header h2 {
  font-size: 2rem;
  font-weight: 500;
  color: #feffb3;
  padding: 1rem 0 1.25rem 0rem;
  line-height: 2.375rem;
  cursor: pointer;
}

.tittle {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.5rem;
  color: #feffb3;
  margin-bottom: 3rem;
  text-align: start;
}

.input {
  background: transparent;
  border: 2px solid #fbe0f3;
  outline: none;
  min-height: 5rem;
  height: 100%;
  max-width: 45.5rem;
  width: 100%;
  font-size: 1.5rem;
  color: #fbe0f3;
  padding: 0 1.5rem 0 1.5rem;
}

.input_wrapper {
  position: relative;
  width: 45.5rem;
}

.input::placeholder {
  color: rgba(251, 224, 243, 0.5);
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: start;
  border: 0;
  letter-spacing: 4px;
  padding: 2rem 0 2rem 0;
}

.btn_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  padding: 1rem 7rem 1rem 7rem;
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 1.75rem;
  background: transparent;
  border: 2px solid #fbe0f3;
  color: #fbe0f3;
  cursor: pointer;
  letter-spacing: 0.25rem;
  display: flex;
  align-items: center;
  border-radius: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 2.5rem;
  position: relative;
}

.btn svg {
  margin-left: 1.5rem;
}

.toggle {
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
  width: 2rem;
  height: 2rem;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: center;
}

.error {
  position: absolute;
  transition: opacity 0.3s ease-in-out;
  transform: translateY(-0.55rem);
  color: rgba(201, 42, 21, 0.85);
  letter-spacing: 4px;
}

.error_border {
  border-color: rgba(201, 42, 21, 0.85);
}
