.container__content {
  padding: 0 3rem 0 3rem;
  max-width: 1905px;
  width: 100%;
  margin: 0 auto;
}

.container__content-item1,
.container__content-item3 {
  display: flex;
  gap: 1.5rem;
}

.container__content-item2 {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  padding: 1.5rem 0 1.5rem 0;
  justify-content: space-between;
}

h2 {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
  text-align: center;
}

span {
  color: #feffb3;
}

.container__content-item1 > :last-child {
  margin-left: auto;
}

.container__content-item2 > :last-child {
  margin-left: 4rem;
}
.container__content-item2 > :nth-child(2) {
  align-self: center;
}

.container__content-item3 > :first-child {
  margin-left: 27.5rem;
}
.container__content-item3 > :last-child {
  margin-left: auto;
}

@media (max-width: 768px) {
  .container__content-item2 h2, .container__content-item2 h2 span {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.25rem;
  }

  .container__content-item1 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: .5rem;
    justify-self: center;
  }

  .container__content-item3 > :first-child {
    margin-left: 0;
  }

  .container__content-item2 {
    grid-template-columns: 1fr 1fr;
    column-gap: 1.5rem;
    row-gap: .5rem;
    padding: .5rem 0;
    margin-top: 1rem;
    justify-self: center;
  }

  .container__content-item2 h2 {
    grid-column: 1/3;
    order: -1;
    margin-bottom: 1rem;
  }

  .container__content-item2 > :last-child, .container__content-item3 > :last-child {
    margin: 0;
    transform: translateY(.5rem);
  }

  .container__content-item3 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-self: center;
  }

  .container__content {
    padding: 2.5rem 1rem;
    max-width: none;
  }

  .container__content-item1 > :last-child {
    margin-left: 0;
  }
}