.input_register_password {
  background: transparent;
  border: 2px solid #fbe0f3;
  outline: none;
  max-width: 45.5rem;
  width: 100%;
  font-size: 1.5rem;
  color: #fbe0f3;
  padding: 1.5rem 4rem 1.5rem 1.5rem;
}

.input_register_password::placeholder {
  color: rgba(251, 224, 243, 0.5);
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: start;
  border: 0;
  letter-spacing: 4px;
}

.register-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input_wrapper-register {
  max-width: 45.5rem;
  width: 100%;
  position: relative;
  height: 5rem;
}

.input_wrapper-register:nth-child(2) {
  margin-top: 3rem;
}

.title_register_password {
  width: 100%;
  text-align: start;
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: 0.25rem;
  margin-bottom: 0.5rem;
}

.successfully-register {
  color: #feffb3;
  font-size: 2rem;
  font-weight: 600;
  margin-top: 3rem;
}

.error {
  color: #eb443c;
  max-height: 0;
  height: 100%;
  overflow: hidden;
  transition:
    max-height 0.3s ease-in-out,
    opacity 0.3s ease-in-out;
  font-size: 1rem;
  padding: 0;
  margin: 0;
  font-weight: 400;
  letter-spacing: 4px;
  opacity: 0;
  text-align: center;
  line-height: 1.5rem;
  white-space: pre-line;
}

.error_border {
  border-color: rgba(201, 42, 21, 0.85);
}

.visible {
  opacity: 1;
  max-height: 1.5rem;
  margin-top: 1rem;
}

.first-error {
  margin-bottom: 1.5rem;
}
