.purpose__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  transition: all 0.3s ease-in-out;
  background: transparent;
  margin: 10rem 0;
}

.purpose__container-p1 {
  line-height: 1.5rem;
  font-weight: 300;
  font-size: 1.5rem;
  color: rgba(251, 224, 243, 0.7);
  letter-spacing: 0.25rem;
}

.purpose__container-h {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
  margin: 2.5rem 0;
}

.purpose__container-p2 {
  font-size: 1.25rem;
  font-weight: 300;
  line-height: 1.25rem;
  color: rgba(251, 224, 243, 0.7);
  letter-spacing: 0.25rem;
}

span {
  color: #feffb3;
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
}

@media (max-width: 768px) {
  .purpose__container-p1 {
    font-size: .75rem;
    font-weight: 300;
    line-height: .75rem;
  }

  .purpose__container {
    margin: 2.5rem 0 5rem 0;
  }
  
  .purpose__container-h, .purpose__container-h span {
    font-size: 1rem;
    line-height: 1.25rem;
  }

  .purpose__container-h {
    margin: 1rem 0;
  }
  
  .purpose__container-p2 {
    font-size: .5rem;
    line-height: .5rem;
    font-weight: 500;
    letter-spacing: 2px;
  }
}